<template>
    <form @submit.prevent="searchSubmit">
        <input placeholder="Card name" v-model="cardName" type="text">
        <button type="submit">Search</button>
    </form>
</template>

<script>
export default {
  name: 'search-bar',
  data () {
    return {
      cardName: ''
    }
  },
  methods: {
    searchSubmit () {
      this.$emit('submit', this.cardName)
    }
  }
}
</script>

