<template>
  <div id="app">
    <div class="header" />
    <div class="container">
      <h1>MTG card price finder</h1>
      <div class="searchRow">
        <SearchBar @submit="searchSubmit" />
      </div>
      <img v-if="searching" class="loader" src="@/assets/loader.gif">
      <p class="noHits" v-if="noHits">No results :(</p>
      <section v-else class="results">
        <div class="cardTemplate" :class="{ cardContainer: card.image_uris, cardContainerHoverState: hover, hidden: !card.image_uris }" v-for="card in cards" :key="card.id" @mouseover="hover = true" @mouseleave="hover = false" v-tilt="{scale: 1.2}">
          <ListItem v-if="card.image_uris" :card="card" />
          <div v-else class="cardSpacer" />
        </div>
      </section>
      <footer />
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import ListItem from './components/ListItem'
import SearchBar from './components/SearchBar'

export default {
  name: 'app',
  components: {
    ListItem,
    SearchBar
  },
  data () {
    return {
      cards: [],
      searching: false,
      noHits: false,
      hover: false
    }
  },
  methods: {
    async searchSubmit (cardName) {
      this.noHits = false
      this.cards = []
      this.searching = true
      const baseURL = 'https://api.scryfall.com/cards/search'
      const url = `${baseURL}?q=${cardName}`
      try {
        const { status, data } = await axios.get(url)
        if (status === 200) {
          data.data.forEach(e => {
            if (e.image_uris) {
              this.cards.push(e)
            }
          })
          for (let i = 0; i < 4; i++) {
            this.cards.push({
              id: i
            })
          }
          this.searching = false
        }
      } catch (e) {
        this.searching = false
        this.noHits = true
      }
    }
  }
}
</script>

